import { notify } from "../../stores/overlay";
const fs = require("fs");
export function createFolderIfDoesntExist(path) {
    if (!fs.existsSync(path)) {
        fs.mkdirSync(path, { recursive: true });
    }
}
export function createFileIfDoesntExist(path, content) {
    if (!fs.existsSync(path)) {
        fs.writeFileSync(path, content);
    }
}
export function saveFile(path, content) {
    fs.writeFileSync(path, content);
}
export function deleteFile(path) {
    try {
        if (fs.existsSync(path)) {
            fs.unlinkSync(path);
        }
    }
    catch (err) {
        notify(5000, err);
    }
}
