import { wordmapsFolder } from "../../util/constants";
const fs = require("fs");
export function save(wordmap) {
    fs.writeFileSync(wordmapsFolder + `${wordmap.id}.wordmap.json`, JSON.stringify(wordmap));
}
export function moveAudioToWordViewFolder(audioPath, wordmapId) {
    fs.copyFileSync(audioPath, wordmapsFolder + `audio/${wordmapId}`);
}
export function moveThumbToWordViewFolder(thumbPath, wordmapId) {
    fs.copyFileSync(thumbPath.replace("file://", ""), `${wordmapsFolder}thumb/${wordmapId}`);
}
export function thumbWasSet(thumb) {
    return thumb.length > 0;
}
export function wordmapOf(uuid) { return (wordmapsFolder + uuid + ".wordmap.json"); }
export function audioOf(uuid) { return (wordmapsFolder + "/audio/" + uuid); }
export function thumbOf(uuid) { return (wordmapsFolder + "/thumb/" + uuid); }
